@charset "UTF-8";
@media (min-width: 1200px) {
  .overlaymenu {
    display: none;
  }
  .popMenu {
    display: none;
    position: absolute;
    top: 70px;
    height: 320px;
    width: 350px;
    left: -230px;
    background: #ffffff;
    z-index: 1;
    border-radius: 10px;
    -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2);
    padding: 10px 10px 20px 10px;
  }
  .popMenu::before {
    content: "";
    width: 0;
    height: 0;
    border: 22px solid transparent;
    border-bottom-color: #ffffff;
    position: absolute;
    right: 20px;
    top: -40px;
  }
  .popMenu .popMenuContainer {
    position: relative;
    width: 100%;
    height: 100%;
  }
  .popMenu .close {
    position: absolute;
    display: inline-block;
    top: 0px;
    right: 20px;
    color: #333;
    font-size: 30px;
  }
  .popMenu .usImage {
    position: absolute;
    top: 10px;
    left: 20px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: whitesmoke;
  }
  .popMenu .usImage img {
    border-radius: 50%;
  }
  .popMenu .usInfo {
    text-align: left;
    width: 100%;
    position: absolute;
    top: 10px;
    left: 130px;
    line-height: 30px;
    color: #666666;
  }
  .popMenu .usInfo p {
    margin-bottom: 0px;
  }
  .popMenu .funcButton {
    position: absolute;
    bottom: 0px;
    width: 100%;
    line-height: 50px;
  }
  .popMenu .funcButton a {
    text-decoration: none;
    line-height: 40px;
    display: inline-block;
    width: 40%;
    margin: 0px 3.5px 0px 3.5px;
    border: 1px solid #f58400;
    border-radius: 7px;
    color: #f58400;
    font-weight: bold;
  }
  .popMenu .funcButton .funcButtonSC {
    width: 85%;
  }
  .popMenu .funcButton a:hover {
    color: white;
    background: #f58400;
  }
  .popMenu .funcButton i {
    padding: 0px 5px 0px;
  }
  .header {
    border-bottom: 1px solid #eee;
    background-color: white;
    height: 60px;
    width: 100%;
    text-align: center;
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
  }
  .header::after {
    content: "";
    height: 59px;
    width: calc((100% - 1200px) / 2);
    background: #f58400 !important;
    position: absolute;
    /*上層沒有relative已body的左上座標為主*/
    top: 0px;
    right: 0px;
  }
  .menuPhone {
    display: none;
  }
  .logoPhone {
    display: none;
  }
  .logoutPhone {
    display: none;
  }
  .logoutPhone1 {
    display: none;
  }
  .headerContainer {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    /*將容器設為flex*/
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    max-width: 1280px;
    /*限制容器最大寬度可避免螢幕過大元件無限變寬*/
    /*使容器寬度等於最大寬大１２８０px*/
    margin: auto;
    /*置中margin自動分配*/
  }
  .logo {
    -ms-flex-preferred-size: 310px;
    flex-basis: 310px;
    /*flex子項寬度*/
    background-color: white;
    height: 59px;
    padding-top: 10px;
  }
  .logo a img {
    height: 40px;
  }
  .menu {
    -ms-flex-preferred-size: 794px;
    flex-basis: 794px;
    /*flex子項寬度*/
    background-color: white;
    height: 59px;
    line-height: 59px;
  }
  .menu ul li {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    padding: 0px 20px;
    font-size: 15px;
  }
  .menu ul li a {
    color: #333;
    text-decoration: none;
  }
  .menu ul .menuli5 a i {
    color: #f08a64;
    width: 30px;
    height: 0.85vw;
  }
  .menu ul .menuli1 a i {
    color: #f08a64;
    width: 30px;
    height: 0.85vw;
  }
  .menu ul .menuli2 a i {
    color: #fdad09;
    width: 30px;
    height: 0.85vw;
  }
  .menu ul .menuli3 a i {
    color: #4b98b3;
    width: 30px;
    height: 0.85vw;
  }
  .menu ul .menuli4 a i {
    color: #5eb559;
    width: 30px;
    height: 0.85vw;
  }
  .menuli1:hover {
    background: #fbe3da;
  }
  .menuli1:hover > a {
    color: #d55b38;
  }
  .menuli2:hover {
    background: #ffefcd;
  }
  .menuli2:hover > a {
    color: #fdad09;
  }
  .menuli3:hover {
    background: #d8e9ef;
  }
  .menuli3:hover > a {
    color: #4b98b3;
  }
  .menuli4:hover {
    background: #ddf1db;
  }
  .menuli4:hover > a {
    color: #5eb559;
  }
  .menuli5:hover {
    background: #fbe3da;
  }
  .menuli5:hover > a {
    color: #d55b38;
  }
  .active1 {
    background: #fbe3da;
  }
  .active2 {
    background: #ffefcd;
  }
  .active3 {
    background: #d8e9ef;
  }
  .active4 {
    background: #ddf1db;
  }
  .menu ul .active1 a {
    color: #d55b38;
  }
  .menu ul .active2 a {
    color: #fdad09;
  }
  .menu ul .active3 a {
    color: #4b98b3;
  }
  .menu ul .active4 a {
    color: #5eb559;
  }
  .fbtn {
    -ms-flex-preferred-size: 150px;
    flex-basis: 150px;
    /*flex子項寬度*/
    background-color: #f58400;
    height: 59px;
    line-height: 59px;
    color: white;
    position: relative;
    z-index: 10;
  }
  .fbtn > a {
    color: white;
  }
  .fbtn > a:hover {
    text-decoration: none;
    cursor: pointer;
  }
}

